import React from "react";
import AwesomeSlider from 'react-awesome-slider';
import styles2 from 'react-awesome-slider/dist/styles.css';
import med from '../illustration/med.svg'
import nacia from '../illustration/nacia.svg'
import soc from '../illustration/soc.svg'
import sport from '../illustration/sport.svg'
import stud from '../illustration/stud.svg'

export default function Carousel(props) {

    return (
        <div className="carousel">
        <h1> </h1>
        <AwesomeSlider bullets={false} className="slider" cssModule={[styles2]} >
        <div>
            <img src={ med } alt="1" />
            <div className="info">
                <h1></h1>
                <p>381,6  </p>
            </div>
        </div>
        <div>
            <img src={ nacia } alt="2" />
            <div className="info">
                <h1> </h1>
                <p>1042,6  </p>
            </div>
        </div>
        <div>
            <img src={ soc } alt="3" />
            <div className="info">
                <h1> </h1>
                <p>551,8  </p>
            </div>
        </div>
        <div>
            <img src={ stud } alt="4" />
            <div className="info">
                <h1></h1>
                <p>479,7  </p>
            </div>
        </div>
        <div>
            <img src={ sport } alt="5" />
            <div className="info">
                <h1>   </h1>
                <p>68,0  </p>
            </div>
        </div>
      </AwesomeSlider>
        </div>
    )
}