import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { HandySvg } from 'handy-svg';
import { Navbar, Nav, Offcanvas} from 'react-bootstrap';
import { NavLink} from 'reactstrap';
import { Link } from 'react-router-dom';
import logo from '../illustration/logo.svg'

export default class NavBar extends Component {
  constructor (props) {
    super(props);

      this.toggleNavbar = this.toggleNavbar.bind(this);

      this.state = {
        collapsed: false,
      };


      window.addEventListener('scroll', function () {
          var minOffset = 945;
          let has_class = document.body.classList.contains("scroll_navbar");
          if (minOffset < window.scrollY) {
              document.body.classList.add('scroll_navbar');
          }
          else if (has_class) {
              document.body.classList.remove('scroll_navbar');
          }
      });
    }


    toggleNavbar () {
        if(window.screen.width < 1000){
            this.setState({
                collapsed: !this.state.collapsed
            });
        }
        window.scrollTo(0,0);
    }
    
    render() {
            return (
                <header className='navbar-outer navbar-expand-lg fixed-top'>
                <Navbar expand="sm" className='container-fluid navbar-inner'>
                    <div className='container'>
                    <Navbar.Brand className='navbar-brand' href="/BudgetMos/"><HandySvg
                                                                                src={logo}
                                                                                className="icon"
                                                                                width="62"
                                                                                height="62"
                                                                            /></Navbar.Brand>
                    <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-sm`} onClick={this.toggleNavbar} />
                    <Navbar.Offcanvas id={`offcanvasNavbar-expand-sm`} aria-labelledby={`offcanvasNavbarLabel-expand-sm`} show={this.state.collapsed} placement="end">
                        <div className='offcanvas-item'>
                        <Offcanvas.Header>
                            <Offcanvas.Title id={`offcanvasNavbarLabel-expand-sm`} className="text-title">
                            {/* <h1 className='text-main'>  </h1> */}
                            <HandySvg
                                src={logo}
                                className="icon"
                                width="142"
                                height="142"
                            />
                            </Offcanvas.Title>
                        </Offcanvas.Header>
                        <Offcanvas.Body>
                            <Nav className="flex-grow-1 pe-3">
                                <NavLink tag={ Link } className = "text-main" to="BudgetMos/" onClick={this.toggleNavbar}></NavLink>
                                <NavLink tag={ Link } className = "text-main" to="BudgetMos/Direction" onClick={this.toggleNavbar}></NavLink>
                            </Nav>
                            <Nav className="flex-grow-2 pe-3">
                                <NavLink tag={ Link } className = "text-main" to="BudgetMos/Contact" onClick={this.toggleNavbar}></NavLink>
                            </Nav>
                        </Offcanvas.Body>
                        </div>
                    </Navbar.Offcanvas> 
                    </div>
                </Navbar>
                </header>
            );
        }
}
